import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import "./Main.css";

export default function Main({ color }) {
  const [color_, setColor] = useState(color);

  useEffect(() => {
    setColor("black");
    return () => {
      setColor("black");
    };
  }, [color]);

  const onClick = (e) => {
    e.preventDefault();
    setColor("black");
    document.getElementById(e.target.name).style.fill = color_;
    e.target.style.color = "white";
    e.target.style.backgroundColor = "black";
  };
  return (
    <>
      <div className="wrap">
        <ul className="region_name">
          <li onClick={onClick}>
            <Link to="/" name="all">
              
            </Link>
          </li>
          <li onClick={onClick}>
            <Link to="/Seoul" name="seoul">
              
            </Link>
          </li>
          <li onClick={onClick}>
            <Link to="/Busan" name="busan">
              
            </Link>
          </li>
          <li onClick={onClick}>
            <Link to="/Daegu" name="daegu">
              
            </Link>
          </li>
          <li onClick={onClick}>
            <Link to="/Gangwon" name="gangwon">
              
            </Link>
          </li>
          <li onClick={onClick}>
            <Link to="/Gyeonggi" name="gyeonggi">
              
            </Link>
          </li>
          <li onClick={onClick}>
            <Link to="/Incheon" name="incheon">
              
            </Link>
          </li>
          <li onClick={onClick}>
            <Link to="/Jeju" name="jeju">
              
            </Link>
          </li>
        </ul>
      </div>
    </>
  );
}
